body {
  height: 100vh; /* 화면 전체 높이에 맞게 설정 */
  display: flex;
  justify-content: center;
  align-items: center;
}

.main {
  display: flex;
  justify-content: center;
}

.Box {
  border:  1px solid black;
  width: 500px;
}

.item-img {
  width: 500px;
  height: 400px;
}

.game-title {
  display: flex;
  justify-content: center;
}


.win {
  border: 10px solid green;
  color: green;
}
.lose {
  border: 10px solid red;
  color: red;
}
.tie {
  border: 10px solid black;
  color: black;
}